  color: #333;
}
body {
  min-width: 1000px;
}
img {
  max-width: 100%;
  max-height: 100%;
}
a {
  color: inherit;
  text-decoration: none;
}
.df {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.fwb {
  font-weight: bold;
}
.g-container {
  width: 1000px;
  margin: 0 auto;
}
.g-header {
  font-size: 32px;
}
.g-header .g-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 80px;
  line-height: 80px;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.g-header .company {
  font-family: cursive;
}
.g-header .tel {
  font-size: 16px;
}
.g-navs {
  -ms-flex-pack: distribute;
      justify-content: space-around;
  font-size: 14px;
  height: 40px;
  background: #2b99ff;
  color: #fff;
}
.g-navs li {
  text-align: center;
  float: left;
  width: 25%;
  line-height: 40px;
  cursor: pointer;
}
.g-navs li:not(:last-child) {
  border-right: 1px solid #0e81ec;
}
.g-navs li:hover,
.g-navs li.active {
  background: #0e81ec;
}
.slides {
  position: relative;
}
.slides li {
  display: block;
}
.slides li img {
  width: 100%;
}
.slides .slides-dots {
  position: absolute;
  width: 100%;
  bottom: 10px;
  text-align: center;
}
.slides .slides-dots li {
  width: 20px;
  height: 20px;
  background: #fff;
  display: inline-block;
  margin-right: 10px;
  cursor: pointer;
}
.slides .slides-dots li.active {
  background: #2b99ff;
}
.display-items {
  font-size: 18px;
}
.display-items .display-items-head {
  border-bottom: 1px solid #929292;
  line-height: 40px;
}
.display-items .display-items-content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.display-items .display-items-content > li {
  margin-top: 20px;
  width: 21%;
}
.display-items .display-items-content > li .desc {
  padding: 0px 5px;
  font-size: 14px;
  line-height: 25px;
  height: 60px;
  overflow: hidden;
}
.display-items .display-items-content > li img {
  transition: all .5s;
  width: 100%;
  height: 210px;
  border: 1px solid #ddd;
}
.display-items .display-items-content > li img:hover {
  -webkit-transform: scale(1.07);
          transform: scale(1.07);
}
@media screen and (max-width: 768px) {
body {
    min-width: 100%;
}
.g-header {
    border-top: 2px solid #2b99ff;
    font-size: 20px;
}
.g-header .g-container {
    text-align: center;
    display: block;
    line-height: 30px;
    height: auto;
    padding: 10px;
}
.g-header .g-container .tel {
    font-size: 14px;
}
.g-header .company,
  .g-header .tel {
    width: 100%;
}
.g-container {
    width: 100%;
    padding: 0 20px;
}
.display-items-content {
    margin: 20px 0;
    display: block!important;
}
.display-items-content > li {
    width: 100%!important;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    aligin-items: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    text-align: center;
    margin-top: 0!important;
}
.display-items-content > li img {
    margin: 0 auto;
    width: 300px!important;
    height: 300px!important;
}
.display-items-content > li .desc {
    height: 40px!important;
    line-height: 40px!important;
}
.slides-dots > li {
    width: 10px!important;
    height: 10px!important;
}
}



